// pos-frontend/src/hooks/printing/usePrint.ts
export type PrintItem = { desc: string; qty: number; price?: number; amount: number };
export type PrintPayload = {
  store?: { name?: string; address1?: string; address2?: string };
  customer?: { name?: string | null };
  cartTotal: number;
  amount: number;
  change: number;
  points?: number;
  items: PrintItem[];
};

export function usePrint() {
  const printReceipt = async (data: PrintPayload) => {
    const origin = typeof window !== 'undefined'
      ? (window.location.origin.includes('localhost') ? 'http://localhost:5000' : window.location.origin)
      : 'http://localhost:5000';

    const url = `${origin.replace(/\/$/, '')}/print/receipt`;
    const res = await fetch(url, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(data),
    });
    if (!res.ok) {
      const t = await res.text().catch(() => '');
      throw new Error(`Print failed: ${res.status} ${t}`);
    }
    return res.json();
  };

  return { printReceipt };
}